.waterfall{padding-right:20px;padding-left:20px}.d-photo{display:inline-block;width:18%;height:180px;margin:8px 10px;cursor:pointer;overflow:hidden;-webkit-transition:all 0.3s ease-out;transition:all 0.3s ease-out}.d-photo img{width:100%;height:100%}.d-photo a{position:relative;top:-180px;display:inline-block;height:230px;width:100%;opacity:0;color:black;-webkit-transition:all 0.3s ease-out;transition:all 0.3s ease-out}.d-photo:hover img{opacity:0.3}.d-photo:hover a{opacity:1;padding-left:50px}.show-more{width:100%;height:50px;text-align:center}.show-more a{display:inline-block;width:120px;height:30px;color:black;margin:auto;padding:2px;border:1px solid black;border-radius:5px}.show-more a i{-webkit-animation:start 0.3s infinite;animation:start 0.3s infinite;padding:5px}@-webkit-keyframes start{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}25%{-webkit-transform:rotate(90deg);transform:rotate(90deg)}50%{-webkit-transform:rotate(180deg);transform:rotate(180deg)}75%{-webkit-transform:rotate(270deg);transform:rotate(270deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes start{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}25%{-webkit-transform:rotate(90deg);transform:rotate(90deg)}50%{-webkit-transform:rotate(180deg);transform:rotate(180deg)}75%{-webkit-transform:rotate(270deg);transform:rotate(270deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@media screen and (max-width: 1060px){.waterfall{width:100%;padding:0;margin:auto}.waterfall .d-photo{width:30%}}@media screen and (max-width: 620px){.waterfall .d-photo{width:45%}}@media screen and (max-width: 420px){.waterfall .d-photo{width:95%}}



