.photo-show{width:100%;height:305px;background-color:white;margin-bottom:20px;padding:8px;text-align:center}.photo-show a.photo-item{width:100%;height:305px;display:block;z-index:1}.photo-show h5{color:black;padding:8px;overflow:hidden;text-overflow:ellipsis;white-space:nowrap}.photo-show .photo-img{height:85%;overflow:hidden;-webkit-transition:all 0.25s ease-out;transition:all 0.25s ease-out}.photo-show .photo-img img{width:100%;height:100%;-webkit-transition:all 0.25s ease-out;transition:all 0.25s ease-out;-webkit-transform:scale(1.1);transform:scale(1.1);background-size:cover}.photo-show a.photo-id{display:none;position:absolute;top:8px;left:22px;z-index:99;color:gray;min-width:100px;height:30px;background-color:white;padding-top:3px}.photo-show a.photo-id i{padding-left:4px;padding-right:4px}.photo-show a.photo-id:hover span{color:#02a388}.photo-show .photo-item:hover img{width:90%;height:94%;opacity:0.8}@media screen and (max-width: 420px){.photo-show a.photo-id:hover span{color:#02a388}.photo-show .photo-item:hover img{width:100%;height:100%;opacity:1}}



