.login .loginBg{background-image:url("../assets/login.jpg");background-position:center center;background-repeat:no-repeat;background-size:cover;left:0;padding-right:40px;position:fixed;text-align:right;width:100%;height:100%}.login .loginBox{position:absolute;height:370px;width:400px;left:50%;top:20%;margin-left:-200px;background:white;box-shadow:2px 3px 11px #333333}.login .loginBox .title{width:100%;height:80px;padding:10px;background-color:black}.login .loginBox .title a{display:block;margin:0 auto;position:relative;font-size:26px;color:white;text-align:center;-webkit-transition:all 0.5s;transition:all 0.5s}.login .loginBox .title a span{margin:auto;display:block;font-size:15px;color:white;opacity:0.9;-webkit-transition:all 0.5s;transition:all 0.5s}.login .loginBox .title a:hover{color:#02a388}.login .loginBox form{margin:auto;width:80%;margin-top:50px}.login .loginBox form button{display:inline-block;width:100%}.error{text-align:center;color:red;font-size:13px;padding-top:10px}@media screen and (max-width: 440px){.login .loginBox{position:absolute;height:360px;width:80%;top:70px;left:230px;background:white;box-shadow:2px 3px 11px #333333}}



