.search{width:800px;height:72px;top:150px;position:relative;background-color:white;margin:auto;overflow:hidden;-webkit-transition:all 0.3s ease-out;transition:all 0.3s ease-out;opacity:0.9}.search .icon{display:inline-block;width:10%;height:100%;position:relative;left:0;top:0;float:left}.search .icon i{padding-left:20%;padding-top:20px;opacity:0.8;cursor:pointer}.search .searchBar{display:inline-block;position:relative;width:70%;height:100%;top:0;float:left}.search .searchBar input{width:90%;height:100%;border:0;font-size:25px;outline:inherit;z-index:1}.search .searchBar i{position:relative;cursor:pointer;top:5px;opacity:0.8;display:none}.search .select{display:inline-block;position:relative;width:20%;height:100%;top:0;float:left;cursor:pointer}.search .select .select-box{margin-top:15px;margin-bottom:15px;height:40px;border-left:1px solid gray;padding-left:30%;padding-top:5px;font-size:20px}.search .select .select-box i{padding-left:10px}.search .select .select-box:hover i{color:#02a388}.option{margin:auto;width:800px;height:0px;display:block;position:relative;top:150px;-webkit-transition:all 0.3s ease-out;transition:all 0.3s ease-out;opacity:0.9;overflow:hidden;z-index:9999}.option ul.show-item{width:20%;height:100%;margin-left:80%;padding-left:0;background-color:white;overflow:hidden;-webkit-transition:all 0.5s ease-out;transition:all 0.5s ease-out}.option ul.show-item li{margin:auto;width:90%;height:50px}.option ul.show-item li a{display:inline-block;width:100%;height:50px;font-size:20px;padding-top:10%;padding-left:30%;color:black}.option ul.show-item li a:hover{color:#02a388}.option ul.show-item li i{padding-right:10px;color:#02a388}@media screen and (max-width: 850px){.searchDiv{-webkit-overflow-scrolling:touch}.searchDiv .search{width:100%;height:60px;top:0;position:relative;background-color:white;margin:auto;overflow:hidden;-webkit-transition:all 0.3s ease-out;transition:all 0.3s ease-out;opacity:0.9}.searchDiv .search .icon{width:10%}.searchDiv .search .icon i{padding-top:15px}.searchDiv .search .searchBar{width:90%}.searchDiv .search .searchBar input{font-size:20px}.searchDiv .search .select{display:none}.searchDiv .option{display:none}}@media screen and (max-width: 440px){.searchDiv .search .icon{width:15%}.searchDiv .search .searchBar{width:85%}.searchDiv .search .searchBar .fa-times{float:right;font-size:26px;margin-top:10px;padding-right:5px}}



