div.imgBox{width:100%}div.imgBox .photos{width:100%;background-position:center center;background-repeat:no-repeat;background-size:cover;left:0;padding-right:40px;position:fixed;text-align:right;width:100%;height:100%}div.imgBox .photos nav{position:fixed;width:100%;height:75px;padding-top:10px;background:rgba(255,255,255,0.3)}div.imgBox .photos nav img{display:inline-block;position:absolute;left:50%;margin-left:-25px;width:60px;height:60px;-webkit-transition:all 0.5s ease-out;transition:all 0.5s ease-out}div.imgBox .photos nav img:hover{width:50px;height:50px}div.imgBox .loading{position:absolute;width:600px;left:50%;margin-top:200px;margin-left:-200px}div.imgBox .loading span{padding-left:20px;font-size:50px}div.imgBox .loading i{float:left;padding-top:10px;font-size:50px;-webkit-animation:start 0.5s infinite ease-in-out;animation:start 0.5s infinite ease-in-out}@-webkit-keyframes start{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}20%{-webkit-transform:rotate(60deg);transform:rotate(60deg)}40%{-webkit-transform:rotate(120deg);transform:rotate(120deg)}60%{-webkit-transform:rotate(180deg);transform:rotate(180deg)}80%{-webkit-transform:rotate(240deg);transform:rotate(240deg)}100%{-webkit-transform:rotate(300deg);transform:rotate(300deg)}}@keyframes start{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}20%{-webkit-transform:rotate(60deg);transform:rotate(60deg)}40%{-webkit-transform:rotate(120deg);transform:rotate(120deg)}60%{-webkit-transform:rotate(180deg);transform:rotate(180deg)}80%{-webkit-transform:rotate(240deg);transform:rotate(240deg)}100%{-webkit-transform:rotate(300deg);transform:rotate(300deg)}}div.imgBox .down nav{position:fixed;top:100%;margin-top:-75px;width:100%;height:75px;padding-top:10px;background:rgba(255,255,255,0.3)}div.imgBox .down nav a{display:block;width:100px;margin:auto;font-size:20px;text-align:center;margin-top:10px;padding:5px;border:1px solid black;color:black;-webkit-transition:all 0.5s ease;transition:all 0.5s ease}div.imgBox .down nav a:hover{border:1px solid red}



