section.menu{width:100%;height:85px;background-color:#0c0d0d;color:#b4b9b9;-webkit-transition:all 0.3s ease-out;transition:all 0.3s ease-out;overflow:hidden}section.menu nav a.active{color:#84e900 !important}section.menu nav.left{padding:0;width:50%;float:left}section.menu nav.right{padding:0;width:50%;float:right}section.menu nav.right ul{float:right}section.menu nav.right ul li{float:left}section.menu nav.right ul li a{color:white;opacity:0.9}section.menu nav.right ul li a:hover{color:#02a388}section.menu li{display:inline-block;font-size:1rem;line-height:85px;padding:0 30px;white-space:nowrap}section.menu li a{-webkit-transition:all 0.3s ease-out;transition:all 0.3s ease-out;color:#b4b9b9}section.menu li a:hover{color:#02a388}section.menu #logo{height:0;margin:0 auto;position:absolute;text-align:center;width:100%}section.menu #logo a{display:block;width:200px;height:58px;margin:0 auto;position:relative;font-size:26px;color:white;padding-top:10px;-webkit-transition:all 0.5s;transition:all 0.5s}section.menu #logo a span{margin:auto;display:block;font-size:15px;color:white;opacity:0.9;-webkit-transition:all 0.5s;transition:all 0.5s}section.menu #logo a.logo:hover{opacity:0.5}section.menu #logo a.logo:hover span{opacity:0.4}section.menu #logo i.fa-bars{display:none}@media screen and (max-width: 968px){section.menu{width:100%;height:auto;-webkit-transition:all 0.3s ease-out;transition:all 0.3s ease-out}section.menu #logo{position:relative;display:block;width:100%;height:80px}section.menu #logo a{width:auto;display:inline-block;text-align:center}section.menu #logo i.fa-bars{display:inline-block;float:right;padding-top:25px;padding-right:20px;margin-left:-20px;cursor:pointer}section.menu .drop{position:relative;width:100%;height:1px;overflow:hidden;-webkit-overflow-scrolling:touch;-webkit-transition:height 0.5s ease;transition:height 0.5s ease;-webkit-transform:transition3d/translateZ}section.menu .drop nav.left,section.menu .drop nav.right{width:100%;position:relative}section.menu .drop nav.left ul,section.menu .drop nav.right ul{width:100%;padding:0;margin:0}section.menu .drop nav.left ul li,section.menu .drop nav.right ul li{display:block;width:100%;height:60px;padding:0;line-height:60px}section.menu .drop nav.left ul li a,section.menu .drop nav.right ul li a{display:inline-block;width:100%;height:60px;padding-left:20px}section.menu .drop nav.left ul li:hover,section.menu .drop nav.right ul li:hover{background-color:rgba(0,0,0,0.5)}}@media screen and (max-width: 420px){section.menu>#logo a.logo:hover{opacity:1}section.menu>#logo a.logo:hover span{opacity:0.9}}



